/*--------------------------------------------------------------
## About Family
--------------------------------------------------------------*/
.entry-header
.entry-content{
    max-width: 100%;
    text-align: center;
    font-family: 'Raleway','Roboto';
}

.wp-block-cover .wp-block-cover__inner-container p{
    font-family: "Great Vibes";
    color: rgba(255, 255, 255, 0.897);
    font-size: 3rem;

}
.entry-content h2{
    font-family: "Great Vibes";
    color: rgba(65, 64, 64, 0.897);
    font-size: 2rem;
}

.about-family-info-wrapper{
    align-items: center;
    margin:0 auto;
}

.about-family-info-wrapper{
   margin-bottom:10%;
}
.about-family-info-wrapper ul{
    padding: 0;
    margin: 0;
 }
.about-family-info-wrapper li{
    list-style: none;
    padding: 0;
    margin-left:0;
 }

 .about-family-text{
     text-align: center;
 }
.about-family-contain-2{
    margin-bottom: 10%;
 }


@media only screen and (min-width: 650px) {
    .about-family-contain-1 ul{
       display: flex;
    }
}